.title {
  margin: 1em 0.5em;
}

.card {
  display: inline-block;
  margin: 1em;
}

.canvas {
  background-color: purple;
  background-image: linear-gradient(
    45deg,
    rgba(255, 255, 255, 1) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 1) 50%,
    rgba(255, 255, 255, 1) 75%,
    transparent 75%,
    transparent
  );
  background-size: 200px 200px;
  border: 1px solid #eee;
}

.option {
  display: inline-block;
  margin: 0 0.5em 1em;
}
